import { Component, Element, Prop, h, Host, Method } from '@stencil/core';

@Component({
	tag: 'o-menu-item',
	styleUrl: 'o-menu-item.scss',
	shadow: true
})
export class OMenuItem {
	private item: HTMLElement;
	@Element() host!: HTMLOMenuItemElement;

	@Prop() value = '';

	@Prop({ reflect: true }) disabled: boolean = false;

	@Prop({ reflect: true }) index: 0 | -1 = -1;

	/* istanbul ignore next */
	connectedCallback() {
		this.host.addEventListener('click', this.handleHostClick);
		this.host.addEventListener('mouseover', this.handleMouseOver);
	}

	/* istanbul ignore next */
	disconnectedCallback() {
		this.host.removeEventListener('click', this.handleHostClick);
		this.host.removeEventListener('mouseover', this.handleMouseOver);
	}

	@Method()
	async setFocus() {
		this.item.focus();
	}

	/* istanbul ignore next */
	private handleHostClick = (event: MouseEvent) => {
		if (this.disabled) {
			event.preventDefault();
			event.stopImmediatePropagation();
		}
	};

	/* istanbul ignore next */
	private handleMouseOver = (event: MouseEvent) => {
		this.host.focus();
		event.stopPropagation();
	};

	render() {
		const disabledClass = this.disabled
			? 'outline-none cursor-not-allowed text-content-disabled'
			: 'hover:bg-background-default-hover cursor-pointer focus:ring-0 focus-visible:ring-focus-ring-color-border-default focus:outline-width-lg focus:outline-focus-ring-color-border-default focus:outline-offset-0';

		return (
			<Host>
				<div
					ref={(el) => (this.item = el)}
					tabIndex={this.index}
					role="menuitem"
					aria-disabled={this.disabled ? 'true' : 'false'}
					class={`relative body-default text-content-default px-4 py-2 ${disabledClass}`}
				>
					<span class="flex items-center gap-2">
						<slot name="startIcon"></slot>
						<slot></slot>
					</span>
				</div>
			</Host>
		);
	}
}
